














* {
  margin: 0;
  padding: 0;
}
ul,
li {
  list-style: none;
}
a {
  text-decoration: none;
}
#app .index-container {
  padding-top: 0.6rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  min-height: 100vh;
  background: url(./static/img/bg.84ce8d4.png) no-repeat;
}
#app .index-container .upload-btn {
  display: block;
  font-size: 0.36rem;
  width: 5.26rem;
  height: 0.92rem;
  background: #2f96ff;
  line-height: 0.92rem;
  color: #fff;
  margin: 0 auto;
  text-align: center;
  border-radius: 10px;
  font-weight: normal;
  -webkit-box-shadow: 0rem 0.1rem 0 0.001rem #448adf;
          box-shadow: 0rem 0.1rem 0 0.001rem #448adf;
  position: relative;
}
#app .index-container .upload-btn::before {
  display: inline-block;
  content: '';
  width: 0.44rem;
  height: 0.36rem;
  background: url(data:image/png;base64,) no-repeat;
  background-size: 100%;
  margin-right: 0.1rem;
  vertical-align: middle;
}
#app .index-container .upload-btn #image-input {
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  outline: none;
  border-radius: 10px;
}
#app .index-container .photo-box {
  margin: 0.4rem auto 0.2rem auto;
  width: 6.4rem;
  height: 6rem;
  background: #fff;
  position: relative;
  overflow: hidden;
}
#app .index-container .photo-box img {
  width: 6.4rem;
  height: 6rem;
  position: absolute;
  z-index: 99;
}
#app .index-container .photo-box .preview-box {
  position: absolute;
  z-index: 9;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
#app .index-container .photo-ul {
  width: 100%;
  height: 2.1rem;
  padding: 0.14rem 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background: #fff;
  text-align: center;
}
#app .index-container .photo-ul img {
  width: 1.6rem;
  height: 1.72rem;
  border-radius: 5px;
}
#app .index-container .photo-ul .select-img {
  border: 2px solid #ff5b2f;
  -webkit-box-shadow: 0px 4px 10px 0 rgba(250, 82, 82, 0.7);
          box-shadow: 0px 4px 10px 0 rgba(250, 82, 82, 0.7);
}
#app .index-container .composite-btn {
  display: block;
  font-size: 0.36rem;
  width: 5.26rem;
  height: 0.92rem;
  background: rgba(243, 111, 84, 0.881);
  line-height: 0.92rem;
  color: #fff;
  margin: 0.2rem auto;
  text-align: center;
  border-radius: 10px;
  font-weight: normal;
  -webkit-box-shadow: 0rem 0.1rem 0 0.001rem #e0836a;
          box-shadow: 0rem 0.1rem 0 0.001rem #e0836a;
}

.share-box {
  text-align: center;
}
.share-box img {
  width: 6.4rem;
  margin: 0 auto;
}
